
<template>
<div>
  <add></add>
  <el-link v-model="logout" style="float:right">退出登录</el-link>
  <el-table
    :data="tableData4"
    style="width: 100%"
    max-height="250">
    <el-table-column
      prop="id"
      label="图书编号"
      align="center"
      >
    </el-table-column>
    <el-table-column
      prop="name"
      label="图书名称"
      align="center"

   >
    </el-table-column>
    <el-table-column
      prop="author"
      label="作者"
      align="center"
     >
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      align="center"
      >
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="deleteRow(scope.$index, tableData4)"
          type="text"
          size="small">
          移除
        </el-button>
      </template>
    </el-table-column>
  </el-table>
  </div>
</template>

<script>
import add from '../components/addButton'
export default {
  methods: {
    deleteRow(index, rows) {
      rows.splice(index, 1);
    }
  },
  data() {
    return {
      tableData4: [
        {
          id: "1",
          name: "java",
          author: "111"
        }
      ]
    };
  }
};
</script>




